.news-container{
  display: block;
  position: relative;
  top: 110px;

  z-index:1;
}

.news-list{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}


/* CSS of the Component NEWS */
.news-component-background{
  background-size: 250px 180px;
  border: solid rgba(0,0,0,0.7);
  width:250px;
  height:180px;
  margin: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.news-component-content{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  color: white;
}

.news-component-content-title{
  display: block;
  padding: 10px;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: bolder;
}

.news-component-content-date{
  display: block;
  padding: 10px;
}
